import React from 'react';
import { Chat, DateRange, Eco, Notifications, People, Person, Settings, SportsEsports } from '@material-ui/icons';

interface MainNavProps {
	selectedTab: number;
	select: (_tab: number) => void;
}

const MainNav = (props: MainNavProps) => {
	return(
		<div className="main-nav">
			<label onClick={() => props.select(1)} className={(props.selectedTab === 1) ? "active" : ""}><Person /></label>
			<label onClick={() => props.select(2)} className={(props.selectedTab === 2) ? "active" : ""}><People /></label>
			<label onClick={() => props.select(3)} className={(props.selectedTab === 3) ? "active" : ""}><DateRange /></label>
			<label onClick={() => props.select(4)} className={(props.selectedTab === 4) ? "active" : ""}><SportsEsports /></label>
			<label onClick={() => props.select(5)} className={(props.selectedTab === 5) ? "active" : ""}><Notifications /></label>
			<label onClick={() => props.select(6)} className={(props.selectedTab === 6) ? "active" : ""}><Chat /></label>
			<label onClick={() => props.select(7)} className={(props.selectedTab === 7) ? "active" : ""}><Settings /></label>
		</div>
	)
}

export default MainNav;

