import React from 'react';
import XboxMenu from '../xboxmenu/XboxMenu';
import './Header.css';
import SystemInfo from './systeminfo/SystemInfo';
import UserInfo from './userinfo/UserInfo';
import  profilePic from '../../images/headshot.png';
import { Avatar } from '@material-ui/core';
import Wifi from '../wifi/Wifi';
import Battery from '../battery/Battery';

export const Header = () => {
    return (
        <div style={{ margin: `0 auto`, maxWidth: 1260, padding: `1.45rem 1.0875rem`, display: 'flex', justifyContent: 'space-between' }}>
            <div id="userInfo" style={{display: 'flex', padding: 5, color: 'white'}}>
                <XboxMenu />
                <Avatar alt="bryceegley.com" src={profilePic} style={{ width: 75, height: 75}} />
                <div style={{marginLeft: 15, display: 'flex'}}>
                    <div style={{margin: 'auto'}}>
                        <div style={{marginBottom: 5}}>bryce.a.egley@gmail.com</div>
                        <div style={{display: 'flex'}}>
                            <span style={{fontSize: '10pt', backgroundColor: 'white', color: 'black', borderRadius: 15, padding: '1px 5px', marginRight: 5}}>G</span>
                            <span>9000+</span>
                        </div>
                    </div>
                </div>
            </div>
            <div style={{marginTop: 'auto', marginBottom: 'auto'}}>
                <div style={{display: 'flex', color: 'white'}}>
                    {/*<IconButton className="menuButton" style={{color: 'white'}}>
                        <NetworkCell />
                    </IconButton>*/}
                    {/*<IconButton className="menuButton" style={{color: 'white'}}>
                        <Wifi />
                    </IconButton>*/}
                    <Wifi />
                    <Battery />
                    <SystemInfo />

                </div>
            </div>
        </div>
    )
}