import { Tooltip } from '@material-ui/core';
import Avatar from '@material-ui/core/Avatar/Avatar';
import { Android, Apps, Chat, Home } from '@material-ui/icons';
import { Link } from 'gatsby';
import React from 'react';
//@ts-ignore
import  profilePic from '../../images/headshot.png';


interface MeProps {}

const Me = (props: MeProps) => {
    return(
        <div id="userInfo" style={{padding: 5, color: 'white'}}>
            <div style={{display: 'flex', marginBottom: 25}}>
                <Avatar alt="Bryce Egley" src={profilePic} style={{ width: 75, height: 75}} />
                <div style={{marginLeft: 15, display: 'flex'}}>
                    <div style={{margin: 'auto'}}>
                        <div style={{marginBottom: 5}}>Bryce Egley</div>
                        <div style={{fontSize: '10pt', color: 'lightgrey', marginBottom: 5}}>What's Up?</div>
                        <div style={{fontSize: '9pt', color: 'grey'}}>(Currently building a React module of the Xbox dashboard, hence the menu with no options)</div>
                    </div>
                </div>
            </div>
            {/*<Navlink name="Home" path="/" />
                        <Navlink name="Blurbs" path="/blurbs/" />
                        <Navlink name="Projects" path="/projects/"/>
    <Navlink name="About" path="/about/"/>*/}
            <Link to="/" style={{ textDecoration: 'none', color: 'white'}}>
                <div className="meOption">
                    <Tooltip title="Wassssuuuuup!">
                        <div style={{display: 'flex'}}>
                            <Home style={{marginRight: 10, color: 'yellow'}} />
                            <div style={{marginTop: 'auto', marginBottom: 'auto'}}>Home</div>
                        </div>
                    </Tooltip>
                </div>
            </Link>
            <Link to="/blurbs" style={{ textDecoration: 'none', color: 'white'}}>
                <div className="meOption">
                    <Tooltip title="Stream of consciousness">
                        <div style={{display: 'flex'}}>
                            <Chat style={{marginRight: 10, color: 'green'}} />
                            <div style={{marginTop: 'auto', marginBottom: 'auto'}}>Blurbs</div>
                        </div>
                    </Tooltip>
                </div>
            </Link>
            <Link to="/projects/" style={{ textDecoration: 'none', color: 'white'}}>
                <div className="meOption"> 
                    <Tooltip title="Behold, my stuff!">
                        <div style={{display: 'flex'}}>
                            <Apps style={{marginRight: 10, color: 'teal'}} />
                            <div style={{marginTop: 'auto', marginBottom: 'auto'}}>Projects</div>
                        </div>
                    </Tooltip>
                </div>
            </Link>
            <Link to="/about/" style={{ textDecoration: 'none', color: 'white'}}>
                <div className="meOption"> 
                    <Tooltip title="In PDF form!">
                        <div style={{display: 'flex'}}>
                            <Android style={{marginRight: 10, color: 'orange'}} />
                            <div style={{marginTop: 'auto', marginBottom: 'auto'}}>About Me</div>
                        </div>
                    </Tooltip>
                </div>
            </Link>
        </div>
    )
}

export default Me;