import { Drawer, IconButton } from '@material-ui/core';
import { Menu } from '@material-ui/icons';
import React from 'react';
import MainNav from './menucomponents/MainNav';
import Me from './Me';
import './XboxMenu.css';
import Games from './Games';

interface Props {

}

interface State {
    renderMenu: boolean;
    selectedTab: number;
}

class XboxMenu extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            renderMenu: false,
            selectedTab: 1
        }

        this.openMenu = this.openMenu.bind(this);
        this.closeMenu = this.closeMenu.bind(this);
        this.toggleMenu = this.toggleMenu.bind(this);
        this.selectTab = this.selectTab.bind(this);
    }

    private toggleMenu() { this.setState(prevState => ({ renderMenu: !prevState.renderMenu })); }
    private openMenu() { this.setState({ renderMenu: true }); }
    private closeMenu() { this.setState({ renderMenu: false }); }

    private selectTab(_tab: number) { this.setState({ selectedTab: _tab }); }
    
    public render() {
        const { selectedTab, renderMenu } = this.state;
        return(
            <>
                <Menu onClick={this.toggleMenu} style={{color: 'darkturquoise', cursor: 'pointer', marginRight: 15, marginTop: 'auto', marginBottom: 'auto'}} />
                {/*<IconButton className="menuButton" style={{color: 'darkturquoise', cursor: 'pointer'}} aria-label="Menu" onClick={this.toggleMenu} >
                    <Menu />
                </IconButton>*/}
                <Drawer anchor="left" open={renderMenu} onClose={this.closeMenu}>
                    <div style={{
                        width: 480,
                        height: '100%',
                        backgroundColor: '#212121',
                        color: 'white',
                        padding: 15
                    }}>
                        <MainNav selectedTab={selectedTab} select={this.selectTab} />
                        { selectedTab === 1 && <Me /> }
                        { selectedTab === 2 && <div>Contacts</div> }
                        { selectedTab === 3 && <div>Calendar</div> }
                        { selectedTab === 4 && <Games /> }
                        { selectedTab === 5 && <div>Notifications</div> }
                        { selectedTab === 6 && <div>Messages</div> }
                        { selectedTab === 7 && <div>Settings</div> }
                    </div>
                </Drawer>
            </>
        )
    }
}

export default XboxMenu;