import { Tooltip } from '@material-ui/core';
import { Battery20, Battery30, Battery50, Battery60, Battery80, Battery90, BatteryAlert, BatteryCharging20, BatteryCharging30, BatteryCharging50, BatteryCharging60, BatteryCharging80, BatteryCharging90, BatteryChargingFull, BatteryFull } from '@material-ui/icons';
import React from 'react';
import { BatterySnackbar } from './BatterySnackbar';
interface Props {}

interface State {
    battery: number;
	charging: boolean;
}

class Battery extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            battery: 100,
			charging: false
        }

		this.chargeBattery = this.chargeBattery.bind(this);
    }

    componentDidMount() {
        setInterval(() => this.updateSystem(), 1000);
    }

    componentWillUnmount() {
        clearInterval();
    }

    private updateSystem() {
		if(!this.state.charging && this.state.battery > 0) {
			let newBatteryState = this.state.battery - 1;
			this.setState({ battery: newBatteryState });
		} else if(this.state.charging && this.state.battery < 100) {
			let newBatteryState = this.state.battery + 1;
			this.setState({ battery: newBatteryState });
		}
    }

	private chargeBattery() {
		this.setState({ charging: true });
	}

	private renderBatteryLife() {
		const { battery, charging } = this.state;
		if(battery >= 95) {
            return charging ? <BatteryChargingFull fontSize="large" style={{color: 'limegreen'}} /> : <BatteryFull fontSize="large" style={{color: 'limegreen'}} />
        } else if (battery >= 90) {
            return charging ? <BatteryCharging90 fontSize="large" style={{color: 'limegreen'}} /> : <Battery90 fontSize="large" style={{color: 'limegreen'}} />
        } else if (battery >= 80) {
            return charging ? <BatteryCharging80 fontSize="large" style={{color: 'limegreen'}} /> : <Battery80 fontSize="large" style={{color: 'limegreen'}} />
        } else if (battery >= 60) {
            return charging ? <BatteryCharging60 fontSize="large" style={{color: 'limegreen'}} /> : <Battery60 fontSize="large" style={{color: 'limegreen'}} />
        } else if (battery >= 50) {
            return charging ? <BatteryCharging50 fontSize="large" style={{color: 'yellow'}} /> : <Battery50 fontSize="large" style={{color: 'yellow'}} />
        } else if (battery >= 30) {
            return charging ? <BatteryCharging30 fontSize="large" style={{color: 'white'}} /> : <Battery30 fontSize="large" style={{color: 'orange'}} />
        } else if (battery >= 5) {
            return charging ? <BatteryCharging20 fontSize="large" style={{color: 'white'}} /> : <Battery20 fontSize="large" style={{color: 'red'}} />
        } else {
            return charging ? <BatteryCharging20 fontSize="large" style={{color: 'white'}} /> : <BatteryAlert fontSize="large" style={{color: 'red'}} />
        }
	}

    public render() {
        const { battery, charging } = this.state;
		return(
			<>
				<Tooltip title={battery + "%"} aria-label="add" style={{fontSize: '12pt'}}>
					<div style={{cursor: 'pointer', marginTop: 'auto', marginBottom: 'auto'}}>
						{this.renderBatteryLife()}
					</div>
				</Tooltip>
				<BatterySnackbar charging={charging} batteryLife={battery} beginCharging={this.chargeBattery} />
			</>
		)
    }	
    
}

export default Battery;