import { Button, Snackbar, IconButton } from "@material-ui/core";
import { Close } from "@material-ui/icons";
import React from "react";

interface Props {
	charging: boolean;
	batteryLife: number;
	beginCharging: () => void;
}

export const BatterySnackbar = (props: Props) => {
	const [open, setOpen] = React.useState(false);

	const handleClick = () => {
		setOpen(true);
	};

	const handleClose = (event: React.SyntheticEvent | React.MouseEvent, reason?: string) => {
		if (reason === 'clickaway') {
			return;
		}
		setOpen(false);
	};

	const charge = () => {
		props.beginCharging();
		setOpen(false);
	}

	if(props.batteryLife === 10 && !props.charging && !open) {
		setOpen(true);
	}

	return (
		<div>
			<Snackbar
				anchorOrigin={{ vertical: 'bottom', horizontal: 'center', }}
				open={open}
				// autoHideDuration={6000}
				onClose={handleClose}
				message="Battery Low!"
				action={
					<>
						<Button color="primary" variant="contained" size="medium" onClick={charge} style={{marginRight: 10}}>
							Begin Charging
			  			</Button>
						<IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
							<Close fontSize="small" style={{color: 'red'}} />
						</IconButton>
					</>
				}
			/>
		</div>
	);
}