import { SignalWifi0Bar } from '@material-ui/icons';
import React from 'react';

interface Props {}

interface State {
    wifiConnected: boolean;
}

class Wifi extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            wifiConnected: false
        }
    }

    componentDidMount() {
        setInterval(() => this.updateSystem(), 1000);
    }

    componentWillUnmount() {
        clearInterval();
    }

    private updateSystem() {
        // this.setState({ time: new Date() });
    }

    public render() {
        return(
            <div style={{cursor: 'pointer', marginTop: 'auto', marginBottom: 'auto'}}>
                <SignalWifi0Bar />
            </div>
        )
    }
    
}

export default Wifi;